
import XHeader from 'vux/src/components/x-header/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import XHr from 'vux/src/components/x-hr/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

var ifycode = ''
var num = 30
var reson = ""
export default {
	components: {
		XHeader,
		Step,
		XButton,
		StepItem,
		XHr,
		XInput,
		Group,
		Divider,
		XButton
	},
	data() {
		return {
			PhoneNumber: '',
			password: '',
			password2: '',
			YzCode: '',
			show1: true,
			show2: false,
			show3: false,
			show4: true,
			succ: false,
			fass: false,
			reson: reson,
			num: num, //默认倒数30秒
		}
	},
	methods: {
		change(val) {
			//    console.log(val)
		},
		getCode() {
			var _this = this;
			var mobile = _this.PhoneNumber
			var title = "用户说明"
			if(mobile == "") {
				return false;
			} else {
				_this.$http.post("http://www.zhongrenjituan.cn/index.php?s=/user/user/send_mobile_verify", _this.$qs.stringify({ mobile: mobile, title: title })).then(function(response) {
					ifycode = response.data.mobis;
					_this.times()
				})
			}
		},
		times() {
			var _this = this;
			if(_this.num <= 1) {
				_this.num = 30
				_this.disable = false
				return false
			} else {
				_this.num--
					_this.disable = true
			}
			setTimeout(function() {
				_this.times()
			}, 1000)

			return _this.num
		},

		nextStep() {
			var mobile = this.PhoneNumber
			var password = this.password
			var repassword = this.password2

			if(this.PhoneNumber == "" || this.YzCode == "" || this.YzCode != ifycode) {
				alert("请输入")
				return false
			} else {
				this.show1 = false
				this.show2 = true
			}
			if(password == "" || repassword == "" || password != repassword) {
				return false
			} else {
				var self = this
				console.log(this)
				this.$http.post("http://www.zhongrenjituan.cn/index.php?s=/user/user/register", this.$qs.stringify({ mobile: mobile, reg_type: 'mobile', password: password, repassword: repassword }))
					.then(function(response) {
						var status = response.data.status
						if(status == 1) {
							self.succ = true
							self.fass = false
						} else {
							self.succ = false
							self.fass = true
							self.reson = response.data.info
						}
					})
				this.show1 = false
				this.show2 = false
				this.show3 = true
				this.show4 = false
			}
		}

	},
}



