
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

const prolist = [{
	src: require('./images/sp02.png'),
	name: '苹果7S plus',
	id: 1,
	charge: '7888',
	text: '土豪金，32G，港货,请包装严实，妥善寄件，附带发票',
	link: "/Detail",
	numberValue: 1,
	checked: false
}, {
	src: require('./images/sp02.png'),
	name: '苹果6S plus',
	id: 2,
	charge: '5888',
	text: '玫瑰红，32G，国产,请包装严实，妥善寄件',
	link: "/Home",
	numberValue: 2,
	checked: false
}]
const checks = [];
const goodsList = [];
export default {
	components: {
		XHeader,
		Group,
		Cell,
		XNumber,
		Checker,
		CheckerItem,
		Divider,
		Panel,
		Swipeout,
		SwipeoutItem,
		SwipeoutButton,
	},
	data() {
		return {
			numberValue: 0,
			error: '',
			prolist: prolist,
			//				amount: '',
			footer: {
				title: '查看更多',
				url: 'http://vux.li'
			},
			checks: []
		}
	},
	computed: {
		amount: function() {
			var amount = 0;
			var selected = this.prolist.filter(function(list) { return list.checked });
			for(let i = 0; i < selected.length; i++) {
				amount += (+selected[i].charge) * (+selected[i].numberValue);
			}
			return amount;
		},
		checkAll: {
			get: function() {
				return this.checkedCount == this.prolist.length;
			},
			set: function(value) {
				this.prolist = this.prolist.map(function(list) { list.checked = value; return list; });
			}
		},
		checkedCount: {
			get: function() {
				return this.prolist.filter(function(list) { return list.checked }).length;
			}
		}
	},
	methods: {
		add(index) {
			this.prolist[index].numberValue++;
		},
		reduce(index) {
			var buy_nums = this.prolist[index].numberValue
			if(buy_nums <= 0) {
				buy_nums = 0;
			} else {
				this.prolist[index].numberValue--;
			}
		},
		currClick(index, event) {
			console.log(event)
		},
	}
}



